import React from 'react'
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js'
import { Doughnut } from 'react-chartjs-2'
import './ChartData.scss'

ChartJS.register(ArcElement, Tooltip, Legend)

export const data = {
  labels: ['Asset', 'Access', 'Change', 'Incidents', 'Service', 'Mobile Sim'],
  datasets: [
    {
      label: 'Total Count',
      data: [12, 19, 3, 5, 2, 3],
      backgroundColor: [
        '#DA5DB6',
        '#5BC4BF',
        '#0DB53C',
        '#F7941D',
        '#77A2C7',
        '#FB7158',
      ],

      borderWidth: 1,
    },
  ],
}

const donutOptions = {
  cutout: '80%',
  width: 400,
  height: 400,
  plugins: {
    legend: { position: 'right' },
  },
}

export default function ChartData() {
  return (
    <div className="position-relative">
      <p className="head_ChartDatas">Top 5 Request Categories</p>
      <div className="ChartData p-3">
        <Doughnut data={data} options={donutOptions} />
        <div className="total position-absolute d-flex align-items-center flex-column justify-content-center">
          <p className="m-0">Total</p>
          <h3>90%</h3>
        </div>
      </div>
    </div>
  )
}
